@include u-keyframes(riseIn) {
  0% {
    opacity: 0;
    @include u-transform(translateY($saf-drop-rise-distance) translateZ(0));
  }
  50% {
    opacity: 1
  }
}

@mixin riseIn($duration: 0.5s, $delay: 0s, $fill-mode: forwards) {
  @include u-animation(riseIn $duration $delay $fill-mode);
}
